main {
  padding: 20px;
  background: #fafafa;
}

.acme-card-wide.mdl-card {
  width: 100%;
}
.acme-card-wide > .mdl-card__title {
  color: #fff;
}
.acme-card-wide > .mdl-card__title > .mdl-card__title-text {
  color: #000;
}
.acme-card-wide > .mdl-card__menu {
  color: #000;
}
.acme-card-wide > .mdl-card__actions {
  text-align: center;
}

.acme-card-square.mdl-card {
  width: 100%;
  min-height: 200px;
}
.acme-card-square > .mdl-card__title {
  color: #fff;
  background: #607D8B;
}
.acme-card-square > .mdl-card__media {
  color: #fff;
  background: none;
  text-align: center;
}
.acme-card-square > .mdl-card__media img {
  height: 100px;
}

.mdl-layout__header-row {
  padding-left: 20px;
}

.mdl-card__supporting-text {
  min-height: 90px;
}

.wender-card-image.mdl-card {
  width: 256px;
  height: 256px;
  background: url("/assets/images/wender.jpg") center/cover;
}

.wender-card-image > .mdl-card__actions {
  height: 52px;
  padding: 10px 0 0 16px;
  background: rgba(0, 0, 0, 0.2);
}

.wender-card-image__name {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}
.wender-card-image__name small {
  display: block;
  font-weight: 100;
  color: #eee;
}

